/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/

h1 {
	// font-size: $font-size-h1;
	font-size: clamp(40px,4.5vw,90px);
	font-weight: $font-weight-m;
	font-family: $secondary-font;
	line-height: 1;
	letter-spacing: 2px;
	@include max-mq ($fhd + 150) {
		font-size: clamp(40px,4.5vw,70px);
	}
	@include max-mq ($mac_l) {
		font-size: clamp(40px,4.5vw,60px);
	}
	// @include min-mq ($lg) {
	// 	letter-spacing: 4px;
	// }
	// @include max-mq ($mac_l) {
	// 	font-size: $font-size-h1 - 0.45;
	// }
	// @include max-mq ($lt) {
	// 	font-size: $font-size-h1 - 0.8;
	// }
	@include max-mq ($lg) {
		font-size: $font-size-h1 - 0.8;
	}
	@include max-mq ($mb_p) {
		font-size: $font-size-h1 - 1.2;
	}
	@include max-mq ($mb) {
		font-size: $font-size-h1 - 1.5;
	}
	@include max-mq ($ip) {
		font-size: $font-size-h1 - 1.5;
	}
	@include max-mq ($xs) {
		font-size: $font-size-h1 - 1.7;
	}
	@include max-mq ($xs - 10) {
		font-size: $font-size-h1 - 1.8;
	}
}
h2 {
	font-size: $font-size-h2;
	font-weight: $font-weight-b;
	font-family: $secondary-font;
	line-height: 1;
	@include max-mq ($ls) {
		font-size: $font-size-h2 - 0.25;
	}
	@include max-mq ($lg) {
		font-size: $font-size-h2 - 0.45;
	}
	@include max-mq ($mb_p) {
		font-size: $font-size-h2 - 0.85;
	}
	@include max-mq ($mb) {
		font-size: $font-size-h2 - 1.35;
	}
	@include max-mq ($ip) {
		font-size: $font-size-h2 - 1.45;
	}
	@include max-mq ($xs) {
		font-size: $font-size-h2 - 1.55;
	}
	@include max-mq ($xs - 10) {
		font-size: $font-size-h2 - 1.6;
	}
}
h3 {
	font-size: $font-size-h3;
	font-weight: $font-weight-sb;
	font-family: $secondary-font;
	line-height: 1.2;
	@include max-mq ($lg) {
		font-size: $font-size-h3 - 0.125;
	}
	@include max-mq ($ip - 20) {
		font-size: $font-size-h3 - 0.35;
	}
}
h4 {
	font-size: $font-size-h4;
	font-weight: $font-weight-sb;
	font-family: $secondary-font;
}
h5 {
	font-size: $font-size-h5;
	font-weight: $font-weight-sb;
	font-family: $secondary-font;
}
h6 {
	font-size: $font-size-h6;
	font-weight: $font-weight-sb;
	font-family: $secondary-font;
}